import React, { Component } from 'react';
import queryString from 'query-string';
import { Grid, Row, Col, FormGroup, FormControl, InputGroup, Glyphicon } from 'react-bootstrap';
import Profile from './Profile';
import Tracks from './Tracks';
import './App.css';

class Search extends Component {
    constructor(props) {
        super(props);

        this.state = {
            token: '',
            query: '',
            artist: null,
            tracks: ['']
        };
    }

    search(event) {
        event.preventDefault();

        const BASE_URL = 'https://api.spotify.com/v1/search?';
        let TRACKS_URL = 'https://api.spotify.com/v1/artists/{id}/top-tracks?country=US';
        const FETCH_URL = BASE_URL + 'q=' + this.state.query + '&type=artist&limit=1';
        const accessToken = this.state.token;

        const options = {
            method: 'GET',
            headers: {
                'Authorization': 'Bearer ' + accessToken
            },
            cache: 'default'
        };

        fetch(FETCH_URL, options)
            .then(response => response.json())
            .then(json => {
                const isError = typeof(json.error);

                isError !== undefined ? this.setState({artist: json.artists.items[0]}) : console.log('Error!', json.error.status, json.error.message);

                TRACKS_URL = TRACKS_URL.replace('{id}', this.state.artist.id);
                console.log(TRACKS_URL);
                fetch(TRACKS_URL, options)
                .then(response => response.json())
                .then(json => this.setState({ tracks: json.tracks }));
            });

        this.setState({query: ''});
    }

    componentDidMount() {
        const queryParams = queryString.parse(this.props.location.search)
        this.setState({
            token: queryParams.access_token
        });
    }

    render() {

        return (
            <Grid className="text-center">
                <Row className="App-title">
                    <Col lg={12}>
                        <h1>MusicMaster</h1>
                    </Col>
                </Row>

                <Row className="Search">
                    <Col lg={12}>
                        <form onSubmit={event => this.search(event)}>
                            <FormGroup>
                                <InputGroup>
                                    <FormControl
                                        type="text" placeholder="Search for an Artist"
                                        value={this.state.query}
                                        onChange={event => this.setState({ query: event.target.value })}
                                    />
                                    <InputGroup.Addon>
                                        <Glyphicon glyph="search" />
                                    </InputGroup.Addon>
                                </InputGroup>
                            </FormGroup>
                        </form>
                    </Col>
                </Row>

                {
                    this.state.artist !== null ? <Profile artist={this.state.artist} /> : <div></div>
                }

                {
                    this.state.tracks[0] !== '' ? <Tracks track={this.state.tracks} /> : <div></div>
                }
            </Grid>
        );
    }
}

export default Search;