import React, { Component } from 'react';
import { Row, Col, Button, Glyphicon } from 'react-bootstrap';
import './App.css';

class Tracks extends Component {
    constructor(props) {
        super(props);

        this.state = {
            playing: false,
            playingUrl: '',
            audio: null
        };
    }

    playAudio(previewUrl) {
        let audio = new Audio(previewUrl);

        if (!this.state.playing) {
            audio.play();

            this.setState({
                playing: true,
                playingUrl: previewUrl,
                audio
            });
        } else {
            this.state.audio.pause();

            if (previewUrl === this.state.playingUrl) {
                this.setState({
                    playing: false,
                    playingUrl: '',
                    audio: null
                });
            } else {
                audio.play();

                this.setState({
                    playing: true,
                    playingUrl: previewUrl,
                    audio
                });
            }
        }
    }

    render() {
        const tracks = this.props.track;
        return(
            <Row className="tracks">
                {
                    tracks.map((track, key) => {
                        return(
                            <Col
                                className="track-item" sm={6} lg={3} key={key}
                                onClick={() => this.playAudio(track.preview_url)}
                            >
                                <img src={track.album.images[0].url} alt={track.name} />

                                <div className="track-overlay">
                                    <h4>{track.name}</h4>
                                    <p>{track.album.name}</p>
                                    <Button>
                                        {
                                            this.state.playingUrl === track.preview_url ? <Glyphicon glyph="pause" /> : <Glyphicon glyph="play" />
                                        }
                                    </Button>
                                </div>
                            </Col>
                        );
                    })
                }
            </Row>
        );
    }
}

export default Tracks;