import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';
import './App.css';

class Profile extends Component {
    render() {
        let artist = {
            name: '',
            followers: {total: ''},
            images: [{ url: '' }],
            genres: ['']
        };

        if (this.props.artist !== null) artist = this.props.artist;

        return(
            <Row className="Profile">
                <Col lg={4}>
                    <img className="profile-image" alt={artist.name} src={artist.images[0].url} />
                </Col>

                <Col lg={8} className="text-left">
                    <Row>
                        <Col lg={12}>
                            <h2>{artist.name}</h2>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg={12}>
                            <h4>{artist.followers.total} followers</h4>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg={12}>
                            <ul className="profile-genres">
                                {
                                    artist.genres.map((mapGenre, key) => {
                                        let genre = artist.genres.length - 1 === key ? mapGenre + '.' : mapGenre + ', ';

                                        return (
                                            <li key={key}>{genre}</li>
                                        );
                                    })
                                }
                            </ul>
                        </Col>
                    </Row>
                </Col>
            </Row>
        );
    }
}

export default Profile;